<template>
   <div>
    <h1>질문 질문 질문 </h1>
    <table class="table table-bordered w-50">
      <tr>
        <th>번호</th>
        <td>{{ qna.qna_no }}</td>
      </tr>
      <tr>
        <th>글쓴이</th>
        <td>{{ qna.qna_userid }}</td>
      </tr>
      <tr>
        <th>제목</th>
        <td>{{ qna.qna_title }}</td>
      </tr>
      <tr>
        <th>내용</th>
        <td>{{ qna.qna_content}}</td>
      </tr>
      <tr>
        <th>날짜</th>
         <td>{{ qna.qna_datetime }}</td>
      </tr>
     
    </table>

    <br />
    <div class="text-center">
      <router-link to="/list"
        ><button class="btn btn-primary">목록</button></router-link>
    
    
    <button class="btn btn-primary" @click="dataDelete">삭제</button>
    <router-link :to="'/update/' +qna.qna_no"><button class="btn btn-primary">수정</button></router-link>
    
    </div>
  </div>
</template>

<script>
import axios from 'axios';
export default {
    name : 'read',
     data: function () {
        return {
            no:0,
        qna: {}
        };
    },
    created() {
        this.no = this.$route.params.no;
        
        axios.get('http://localhost:8080/ssafy/api/qna/'+this.no)
        .then((response) => {this.qna = response.data;})
     
    },
    methods:{
        dataDelete(){
            axios.delete('http://localhost:8080/ssafy/api/qna/'+this.no)
            .then(() => {alert('삭제가 완료되었습니다.');
                        this.moveList();
                        
                        })
           
        }//search

      , moveList() {
      this.$router.push('/list');
        },
    }//methods
}
</script>

