<template>
  <div>
        <h1>질문 목록 게시판 </h1>
      <div class="text-right">
  
        <button class="btn btn-primary" @click="movePage">등록</button>
      </div>
      <div v-if="qnas.length">
        <table class="table table-bordered table-condensed">
          <colgroup>
            <col :style="{width: '20%'}"/>
            <col :style="{width: '20%'}"/>
             <col :style="{width: '25%'}"/>
            <col :style="{width: '25%'}"/>
          </colgroup>
          <tr>
            <th>번호</th>
            <th>작성자</th>
            <th>제목</th>
             <th>작성일</th>
          </tr>
          <tr v-for="(qna, index) in qnas" :key="index + '_qnas'">
            <td>

             <router-link :to="'read/'+qna.qna_no">{{ qna.qna_no }}</router-link>
   
            </td>
                <td>{{qna.qna_userid}}</td>
                <td>{{qna.qna_title}}</td>
                <td>{{qna.qna_datetime}}</td>
          </tr>
        </table>
      </div>
      <div v-else>
        글이 없습니다.
      </div>



      
    </div>
</template>

<script>
import axios from 'axios';
import 'expose-loader?$!expose-loader?jQuery!jquery';
import 'bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
export default {
    
    name : 'List',

    data: function () {
    return {
      qnas: [],
    };
  },
  created() {
    axios.get('http://localhost:8080/ssafy/api/qna').then(({ data }) => {
      this.qnas = data;
    });
  },
  methods: {
   
    movePage() {
      this.$router.push('/create');
    },
  },
}
</script>

