<template>
   <div>
    <div class="form-group">
      <label for="qna_title">title</label>
      <input type="text" class="form-control" id="qna_title" ref="qna_title"  v-model="qna_title">
    </div>
       <div class="form-group">
      <label for="qna_content">내용</label>
      <input type="text" class="form-control" id="qna_content" ref="qna_content"  v-model="qna_content">
    </div>
       <div class="form-group">
      <label for="qna_userid">user_id</label>
      <input type="text" class="form-control" id="qna_userid" ref="qna_userid"  v-model="qna_userid">
    </div>
       
    
    <div class="text-right">
      <button class="btn btn-primary" @click="checkHandler">등록</button>
      <button class="btn btn-primary" @click="moveList">목록</button>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
export default {
    name : 'create',
    
    data: function () {
    return {
      qna_title : '',
      qna_content : '',
      qna_userid : '',
   
    };
  },
  methods: {
    checkHandler() {
      let err = true;
      let msg = '';
      !this.qna_title &&
        ((msg = '제목를 입력해주세요'),
        (err = false),
        this.$refs.qna_title.focus());
      err &&
        !this.qna_content &&
        ((msg = '내용 입력해주세요'), (err = false), this.$refs.qna_content.focus());
      err &&
        !this.qna_userid &&
        ((msg = 'id 입력해주세요'),
        (err = false),
        this.$refs.qna_userid.focus());

      if (!err) alert(msg);
      else this.createHandler();
    },
    createHandler() {
      axios
        .post('http://localhost:8080/ssafy/api/qna/create', {
          qna_title:this.qna_title,
          qna_content: this.qna_content,
          qna_userid: this.qna_userid,
        })
        .then(({ data }) => {
          let msg = '등록 처리시 문제가 발생했습니다.';
          if (data === 'success') {
            msg = '등록이 완료되었습니다.';
          }
          alert(msg);
          
          this.moveList();
        });
    },
    moveList() {
      this.$router.push('/list');
    },
  },
}
</script>


